<template>
  <div class="box">
    <bread :msg1="'商品管理'" :msg2="'添加商品'"></bread>
    <el-card class="box-card" :body-style="{ padding: '20px', height: '100%' }">
      <!-- 顶部提示信息 -->
      <el-alert
        title="添加商品信息"
        type="info"
        :closable="false"
        :center="true"
        :show-icon="true"
      >
      </el-alert>

      <!-- 步骤条 -->
      <el-steps
        :space="200"
        :active="active"
        finish-status="success"
        :align-center="true"
        style="font-size:12px;"
      >
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>

      <!-- 标签页 -->
      <el-tabs
        :tab-position="'left'"
        style="height: 200px;"
        @tab-click="cutOver($event, 'formMsg')"
        :before-leave="changeTab"
      >
        <!-- 基本信息 -->
        <el-tab-pane label="基本信息" name="0">
          <template>
            <el-form
              :label-position="'top'"
              label-width="80px"
              :model="formData"
              :rules="rules"
              ref="formMsg"
            >
              <el-form-item label="商品名称" prop="goods_name">
                <el-input v-model="formData.goods_name"></el-input>
              </el-form-item>
              <el-form-item label="商品价格" prop="goods_price">
                <el-input v-model="formData.goods_price"></el-input>
              </el-form-item>
              <el-form-item label="商品重量" prop="goods_weight">
                <el-input v-model="formData.goods_weight"></el-input>
              </el-form-item>
              <el-form-item label="商品数量" prop="goods_number">
                <el-input v-model.number="formData.goods_number"></el-input>
              </el-form-item>
              <el-form-item label="商品分类">
                <div class="block">
                  <el-cascader
                    v-model="value"
                    :options="options"
                    :props="props"
                    :clearable="true"
                  ></el-cascader>
                </div>
              </el-form-item>
            </el-form>
          </template>
        </el-tab-pane>
        <!-- 商品参数 -->
        <el-tab-pane label="商品参数" name="1">
          <div
            style=" width: 100%;  height: 100%"
            v-for="item in activeData"
            :key="item.attr_id"
          >
            <div style="color: #333; font-size: 14px;  width: 100%;">
              {{ item.attr_name }}
            </div>
            <el-checkbox
              v-for="items in item.attr_vals.split(',')"
              :label="items"
              border
              :key="items"
              style="margin: 10px 0 0 10px; font-size: 12px"
            >
            </el-checkbox>
          </div>
        </el-tab-pane>
        <!-- 商品属性 -->
        <el-tab-pane label="商品属性" name="2">
          <div
            style=" width: 100%;  height: 100%;margin-bottom: 15px"
            v-for="item in staticData"
            :key="item.attr_id"
          >
            <div
              style="color: #333; font-size: 14px;  width: 100%;margin-bottom: 5px;"
            >
              {{ item.attr_name }}
            </div>
            <el-input v-model="item.attr_vals"></el-input>
          </div>
        </el-tab-pane>
        <!-- 商品图片 -->
        <el-tab-pane label="商品图片" name="3">
          <el-upload
            class="upload-demo"
            :action="actions"
            :file-list="fileList"
            list-type="picture"
            :headers="headers"
            :on-success="handlerSuccess"
          >
            <el-button size="small" type="primary">点击上传</el-button>
            <div slot="tip" class="el-upload__tip">
              只能上传jpg/png文件，且不超过500kb
            </div>
          </el-upload>
        </el-tab-pane>
        <!-- 商品内容 -->
        <el-tab-pane label="商品内容" name="4">
          <quill-editor
            v-model="formData.goods_introduce"
            style="margin-bottom:20px;width:100%;height:300px;"
          >
          </quill-editor>
          <el-button type="primary" @click="addGoods" style="margin-top: 60px;">
            添加商品
          </el-button>
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    /* 判断价格数据是否合规 */
    var checkPrice = (rule, value, callback) => {
      if (!/^((\d+\.\d+)|([123456789]\d*))$/.test(value)) {
        callback(new Error('价格应该是数字'))
      }
      callback()
    }
    /* 判断重量数据是否合规 */
    var checkWeight = (rule, value, callback) => {
      if (!/^((\d+\.\d+)|([123456789]\d*))$/.test(value)) {
        callback(new Error('重量应该是数字'))
      }
      callback()
    }
    return {
      flag: false,
      active: 0,
      formData: {
        goods_name: '123',
        goods_price: '1',
        goods_weight: '1',
        goods_number: '1',
        pics: [],
        attrs: '',
        goods_cat: '',
        goods_introduce: ''
      },
      value: [],
      options: [],
      props: {
        expandTrigger: 'hover',
        label: 'cat_name',
        value: 'cat_id',
        children: 'children'
      },
      rules: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' },
          { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' },
          { validator: checkPrice, trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '请输入商品重量', trigger: 'blur' },
          { validator: checkWeight, trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '请输入商品数量', trigger: 'blur' },
          { type: 'number', message: '商品数量应为数字', trigger: 'blur' }
        ]
      },
      formMsg: {},
      activeData: [],
      staticData: [],
      // 上传图片
      fileList: [
        {
          name: 'food.jpeg',
          url:
            'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
        }
      ],
      headers: {
        Authorization: localStorage.getItem('token')
      },
      actions: ''
    }
  },
  created () {
    this.getOptions()
    this.actions = this.$axios.defaults.baseURL + '/upload'
  },
  methods: {
    /* 切换tab栏 钩子函数 */
    changeTab (a, b) {
      if (b === '0') {
        this.formData.goods_cat = this.value.join(',')
        return this.flag
      }
    },
    /* 切换步骤条状态 */
    cutOver (e) {
      if (this.flag) {
        this.active = e.name - 0
        switch (e.name) {
          case '1': {
            // 动态参数
            const obj = { id: this.value[2], params: { sel: 'many' } }
            const paneOne = this.getAttr(obj)
            paneOne.then(res => {
              if (res.data.meta.status === 200) {
                this.activeData = res.data.data
              }
            })
            break
          }
          case '2': {
            // 静态参数
            const obj = { id: this.value[2], params: { sel: 'only' } }
            const paneTwo = this.getAttr(obj)
            paneTwo.then(res => {
              if (res.data.meta.status === 200) {
                this.staticData = res.data.data
              }
            })
            break
          }
          case '3': {
            break
          }
        }
      } else {
        this.$message({ message: '请输入商品信息', type: 'error' })
      }
    },
    /* 获取数据 */
    async getOptions () {
      const res = await this.$axios.get('/categories', { params: { type: 3 } })
      if (res.data.meta.status === 200) {
        this.options = res.data.data
      } else {
        this.$message({ message: res.data.meta.msg, type: 'error' })
      }
    },
    /* 获取商品参数 */
    async getAttr (obj) {
      const res = await this.$axios.get(`categories/${obj.id}/attributes`, {
        params: obj.params
      })
      return res
    },
    /* 添加图片 */
    handlerSuccess (response, file, fileList) {
      const tmpPath = ('/' + response.data.tmp_path).replace(
        'uploads\\',
        'uploads/'
      )
      this.formData.pics.push({ pic: tmpPath })
    },
    /* 添加商品 */
    async addGoods () {
      const res = await this.$axios.post('/goods', this.formData)
      if (res.data.meta.status === 201) {
        this.$message({ message: '添加成功', type: 'success' })
      } else {
        this.$message({ message: res.data.meta.msg, type: 'error' })
      }
    }
  },
  watch: {
    formData: {
      handler (newName) {
        if (
          newName.goods_name.length === 0 ||
          newName.goods_price.length === 0 ||
          newName.goods_weight.length === 0 ||
          newName.goods_number.length === 0 ||
          this.value.length !== 3
        ) {
          this.flag = false
        } else {
          this.flag = true
        }
      },
      deep: true,
      immediate: true
    },
    value: {
      handler (a, b) {
        if (a.length !== 3) {
          this.flag = false
        } else {
          this.flag = true
        }
      },
      immediate: true
    }
  }
}
</script>

<style lang="less" scoped>
.box,
.el-tabs {
  height: 100%;
}
.el-tabs--left {
  height: 100% !important;
}
.el-steps {
  margin-top: 15px;
  /deep/.el-step__title {
    font-size: 12px;
  }
  /deep/.el-step__icon {
    font-size: 12px;
  }
}
.el-tabs {
  margin-top: 20px;
}
</style>
